<template>
  <div class="container-fluid mb-4">
    <!-- <hotarea></hotarea> -->
    <div class="hot-area">
      <h2>熱門行政區</h2>
      <div class="row">
        <div class="col-sm-3 col-xs-6 ">
          <button class="btn hot-btn btn-primary btn-block" @click="hotarea('苓雅區')">苓雅區</button>
        </div>
        <div class="col-sm-3 col-xs-6 ">
          <button class="btn hot-btn btn-info btn-block" @click="hotarea('三民區')">三民區</button>
        </div>
        <div class="col-sm-3 col-xs-6 ">
          <button class="btn hot-btn btn-danger btn-block" @click="hotarea('新興區')">新興區</button>
        </div>
        <div class="col-sm-3 col-xs-6 ">
          <button class="btn hot-btn btn-warning btn-block" @click="hotarea('甲仙區')">甲仙區</button>
        </div>
      </div>
    </div>
    <section >
      <div class="line">
      </div>
       <div class="row mx-auto" style="width:95%" >
        <div class="col-lg-4 mt-4" style="max-width:95%" v-for="(item) in items[currentPage]" :key="item.Name"  >
          <div class="card mx-auto" >
            <div class="card-header bg-cover" :style="{backgroundImage:`url(${item.Picture1})`}" >
              <h3>{{item.Name}}</h3>
            </div>
            <div class="card-body">
              <p class="card-text mb-1"><i class="fas fa-clock mr-1"></i>開放時間：{{item.Opentime}} </p>
              <p class="card-text mb-1"><i class="fas fa-map-marker-alt mr-1"></i>地點位置：{{item.Add}} </p>
              <p class="card-text mb-1"><i class="fas fa-phone mr-1"></i>聯絡電話：{{item.Tel}} </p>
              <p class="card-text mb-1" v-if="item.Ticketinfo"><i class="fas fa-ticket-alt mr-1"></i>門票資訊：{{item.Ticketinfo}} </p>
              <div class="card-info">
                <button class="btn hot-btn mt-2 btn-info" @click="modalinfo(item)" >詳細資訊</button>
              </div>
            </div>
          </div>
        </div>
        <div class="col-12 mt-5">
          <nav aria-label="Page navigation example mx-auto ">
            <ul class="pagination justify-content-center">
              <li class="page-item" :class="{disabled : currentPage<=0}" @click.prevent="currentpage(currentPage-1)">
                <a class="page-link" href="#" aria-label="Previous" >
                  <span aria-hidden="true" >&laquo;</span>
                </a>
              </li>
              <li class="page-item" v-for="(num,idx) in items.length" :key="num" :class="{active:currentPage ===idx }" @click.prevent ="currentpage(idx)" >
                <a class="page-link" href="#">
                  {{num}}
                </a>
              </li>
      
              <li class="page-item" :class="{disabled : currentPage >=items.length-1}" @click.prevent="currentpage(currentPage+1)">
                <a class="page-link" href="#" aria-label="Next">
                  <span aria-hidden="true">&raquo;</span>
                </a>
              </li>
            </ul>
          </nav>
        </div>
      </div>
      
    </section>
    <!-- modal -->
    <modal>
       <h5 slot="modal-header" class="modal-title" >{{modal_info.Name}}</h5>
       <iframe slot="modal-map"  class="w-100 mt-2" height='250' frameborder='0' scrolling='no' marginheight='0' marginwidth='0' :src="`https://maps.google.com.tw/maps?output=embed&q=${modal_info.Py},${modal_info.Px}${modal_info.Name}`"></iframe>
       <img slot="modal-bg" class="img-fluid text-center" :src="modal_info.Picture1" :alt="`${modal_info.Name}`">
       <p slot="modal-open" class="mt-2"><i class="fas fa-map-marked-alt"></i> 開放時間: {{modal_info.Opentime}}</p>
       <p slot="modal-descr"><i class="far fa-bookmark mr-1"></i>地點簡介：{{modal_info.Description}}</p>
    </modal>
  </div>
</template>


<script>
import hotarea from "./hot-area";
import modal from './modal';
import $ from "jquery";
export default {
  name: "travel",
  props: ["items",'currentPage'],
  data() {
    return {
      modal_info:[],
    };
  },
  components: {
    modal
    // hotarea
  },
  methods: {
    modalinfo(item) {
      console.log(item);
      this.modal_info = item ;
      console.log(this.modal_info);
      $('#myModal').modal('show')
    },
    currentpage(num){
      if(num<0) return
      if(num>this.items.length-1) return
      this.$emit('changePage',num)
    },
    hotarea(hotName){
      this.$emit('hotarea',hotName)
    }
  }
};
</script>



<style scope lang="scss">
@import url("https://fonts.googleapis.com/earlyaccess/cwtexyen.css");
.line {
  position: relative;
  margin: auto;
  width: 95%;
  height: 20px;
  padding: 30px;
  border-bottom: 3px dashed rgb(42, 136, 190);
  &::after {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 77%;
    font-size: 22px;
    width: 30px;
    height: 30px;
    border: 2px solid rgb(42, 136, 190);
    border-radius: 100%;
    text-align: center;
    background: #fff;
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    content: "\f107";
  }
}
.card {
  box-shadow: 0 27px 55px 0 rgba(0, 0, 0, 0.3),
    0 17px 17px 0 rgba(0, 0, 0, 0.15);
  height: 100%;
  .card-body {
    position: relative;
    .card-info {
      position: absolute;
      bottom: 4%;
    }
  }
}
.card-header {
  min-height: 180px;
  background-size: cover;
  background-position: center;
  position: relative;
  h3 {
    font-family: "cwTeXYen", sans-serif;
    position: absolute;
    left: 5px;
    bottom: 0;
    font-size: 20px;
    color: #8efbfd;
  }
}
.fas {
  font-size: 20px;
}
.fa-clock {
  color: rgb(42, 136, 190);
}
.fa-map-marker-alt {
  color: #6a90cd;
}
.fa-phone {
  color: #9de1cd;
}
.fa-ticket-alt {
  color: #1fff7b;
  //  margin-bottom: 50px;
}
.fa-map-marked-alt{
  color: #b5e8a9;
}
@media (min-width: 1200px) {
  .card {
    min-height: 390px;
  }
}
@media (max-width: 1200px) and (min-width: 992px) {
  .card {
    min-height: 410px;
  }
}
@media (max-width: 992px) and (min-width: 520px) {
  .card {
    min-height: 390px;
  }
}
@media (max-width: 520px) {
  .card {
    min-height: 410px;
  }
}


//熱門行政區
.hot-area{
 margin-top: -70px;
 padding: 30px;
 background-color: #fff;
 box-shadow: 3px 4px 4px 0px #888888;
 opacity: .95;
 border-radius: 10px;
 color: #fff;
 h2{
   background: -webkit-linear-gradient(top, red 0, blue 100%);
   font-weight: normal;
   -webkit-background-clip: text;
   -webkit-text-fill-color: transparent;
   color: blue;
   text-align: center;
   margin-bottom: 30px;
 }
}
.hot-btn{
    position: relative;
    top: -6px;
    border: 0;
    transition: all 40ms linear;
    color: #fff;
    opacity: 1;
    transition: opacity .3s;
    &:hover{
      opacity: .75;
      color: #fff;
    }
    &.btn-primary{
      box-shadow: 0 0 0 1px #417fbd inset, 0 0 0 2px rgba(255, 255, 255, 0.15) inset, 0 8px 0 0 #4D5BBE, 0 8px 8px 1px rgba(0, 0, 0, 0.5);
	    background-color: #4274D7;
    }
    &.btn-primary:active{
      box-shadow: 0 0 0 1px #417fbd inset, 0 0 0 1px rgba(255, 255, 255, 0.15) inset, 0 1px 3px 1px rgba(0, 0, 0, 0.3); 
	    background-color: #4274D7;
    }
    &.btn-info{
      box-shadow: 0 0 0 1px #00a5c3 inset, 0 0 0 2px rgba(255, 255, 255, 0.15) inset, 0 8px 0 0 #348FD2, 0 8px 8px 1px rgba(0, 0, 0, 0.5);
	    background-color: #39B3D7;
    }
    &.btn-info:active{
      box-shadow: 0 0 0 1px #00a5c3 inset, 0 0 0 1px rgba(255, 255, 255, 0.15) inset, 0 1px 3px 1px rgba(0, 0, 0, 0.3);
    	background-color: #39B3D7;
    }
    &.btn-danger{
      box-shadow: 0 0 0 1px #b93802 inset, 0 0 0 2px rgba(255, 255, 255, 0.15) inset, 0 8px 0 0 #AA0000, 0 8px 8px 1px rgba(0, 0, 0, 0.5);
    	background-color: #D73814;
    }
    &.btn-danger:active{
      box-shadow: 0 0 0 1px #b93802 inset, 0 0 0 1px rgba(255, 255, 255, 0.15) inset, 0 1px 3px 1px rgba(0, 0, 0, 0.3);
	    background-color: #D73814;
    }
    &.btn-warning{
      box-shadow: 0 0 0 1px #d79a47 inset, 0 0 0 2px rgba(255, 255, 255, 0.15) inset, 0 8px 0 0 #D79A34, 0 8px 8px 1px rgba(0, 0, 0, 0.5);
	    background-color: #FEAF20;
    }
    &.btn-warning:active{
      box-shadow: 0 0 0 1px #d79a47 inset, 0 0 0 1px rgba(255, 255, 255, 0.15) inset, 0 1px 3px 1px rgba(0, 0, 0, 0.3);
      background-color: #FEAF20;
      color: #fff!important;
    }
  }
  .hot-btn:active:focus,
  .hot-btn:focus:hover,
  .hot-btn:focus {
  outline-style: none;
  outline: medium none;
  }
  .hot-btn:active,
  .hot-btn.active {
    top: 2px;
  }

@media (max-width: 576px) {
  .btn{
    margin-top: 15px;
  }
}

// 分頁
.page-link{
  display: flex;
  width: 30px;
  height: 30px;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  border-radius: 100%;
}
.page-item:first-child .page-link{
  margin-left: 0;
  border-top-right-radius: 0.25rem;
  border-bottom-right-radius: 0.25rem;
  border-top-left-radius: 1rem;
  border-bottom-left-radius: 1rem;
}


.page-item:last-child .page-link{
  border-top-left-radius: 0.25rem;
  border-bottom-left-radius: 0.25rem;
  border-top-right-radius: 1rem;
  border-bottom-right-radius: 1rem;
}


</style>



