<template>
  <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true" v-cloak>
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <slot name="modal-header"></slot>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="w-100">
            <slot name="modal-map"></slot>
          </div>
        <div class="modal-body p-1">
          <slot name="modal-bg"></slot>
          <slot name="modal-open"></slot>
        </div>
        <div class="modal-footer">
          <slot name="modal-descr"></slot>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
 name: 'modal', 
}
</script>

<style>
@import url(https://fonts.googleapis.com/earlyaccess/notosanstc.css);
.modal{
  letter-spacing: 1.6px;
}
.modal-title{
  font-family: 'Noto Sans TC', sans-serif;
  font-weight: 800;
}
.modal-footer{
  text-align: justify
}
.fa-bookmark{
  color: #bec6f7;
}
/* modal */
@media (min-width: 576px){
  .modal-dialog {
    width: 65%;
    max-width: 800px;
    margin: 1.75rem auto;
  }
}
.modal-content{
  border:0px solid;
  box-shadow:6px 4px 5px 0px #6c7d7a;
}
</style>


