<template>
  <header class="container-fluid" :style="{backgroundImage:`url(${bg2})`}">
    <div class="container">
      <h1>Kaohsiung travel</h1>
      <div id="mainselection">
        <select @input="changeZone">
          <option value=''>--- 請選擇行政區域 ---</option>
          <option v-for="location in locations" :key="location" :value="location">{{location}}</option>
        </select>
      </div>
    </div>
  </header>
</template>

<script>
import bg2 from '../images/header-kv.png'
export default {
  name: 'headerinput',
  props:['locations'],
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      bg2,
    }
  },
  methods:{
    changeZone(e){
      this.$emit('changeZone',e.target.value)
      console.log('emit',e.target.value);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import url('https://fonts.googleapis.com/css?family=Shadows+Into+Light');

header{
  height: 400px;
  background-image: url('../images/header-kv.png');
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
  h1{
    font-family: 'Shadows Into Light', cursive;
    text-align: center;
    color: #fff;
    letter-spacing: 3px;
  }
  #mainselection select {
   border: 0;
   color: #EEE;
   background: rgba( #315cd3,.95) url("http://i62.tinypic.com/15xvbd5.png") no-repeat  98% ;
   font-size: 20px;
   font-weight: bold;
   padding: 2px 10px;
   width: 100%;
   -webkit-appearance: none;
}

  #mainselection {
    margin-top: 30px;
    overflow:hidden;
    width:100%;
    -moz-border-radius: 9px 9px 9px 9px;
    -webkit-border-radius: 9px 9px 9px 9px;
    border-radius: 9px 9px 9px 9px;
    box-shadow: 1px 1px 11px #330033;
  }

}
</style>



