<template>
  <div id="app">
    <headerinput :locations="locations" @changeZone="changeZone"></headerinput>
    <travel-content :items="filterData" :currentPage="currentPage" @changePage="changePage" @hotarea="hotarea"></travel-content>
  </div>
</template>

<script>
import headerinput from './components/header'
import travelContent from './components/travel-content'

export default {
  name: 'App',
  data(){
    return{
      items:[],
      currentPage:0,
      locations:[],
      currentZone:'',
    }
  },
  computed:{
    filterData(){
      let vm = this;
      let data = [];

      if(vm.currentZone !==''){
        vm.currentPage = 0;
        data = vm.items.filter((item)=>{
          return item.Zone ===  vm.currentZone
        })
        }else{
          vm.currentPage = 0;
          data = vm.items
        }
      const pageData= [];
      data.forEach((item,idx)=>{
        if(idx % 9 ===0){
          pageData.push([])
        }
        const page = parseInt(idx/9);
        pageData[page].push(item);
      })
      console.log(pageData);
      return pageData
    },
  },
  components: {
    headerinput,
    travelContent
  },
  methods:{
    getdata(){
      var vm = this;
      this.axios.get('https://data.kcg.gov.tw/api/action/datastore_search?resource_id=92290ee5-6e61-456f-80c0-249eae2fcc97')
      .then(function (response) {
        console.log(response.data.result.records);
        vm.items =response.data.result.records;
        vm.getZone()
      })
      .catch(function (error) {
        console.log(error);
      });
    },
    changePage(num){
      this.currentPage = num;
    },
    getZone(){
      const vm =this;
      const locations = new Set();
      vm.items.forEach((item)=>{
        locations.add(item.Zone)
      })
      vm.locations = Array.from(locations)
      console.log( vm.locations)
    },
    changeZone(zone){
      console.log('get',zone);
      this.currentZone = zone ;
       console.log('currentzone',this.currentZone);
    },
    hotarea(hotName){
      this.currentZone =hotName
    }
  },
  created(){
    this.getdata()
  }
}
</script>

<style lang="scss">
@import "~bootstrap/scss/bootstrap";
#app {
  font-family: '微軟正黑體';
}
</style>



